<template>
  <div class="p5MotionDirection">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import P5NavBar from "@/components/P5NavBar.vue";

export default {
  components: {
    Header,
    P5NavBar
  },
  data() {
    return {
      script: null,
      ps: null,
      canvas: null,
      mover: null
    };
  },
  mounted() {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight);
        this.canvas.parent(this.$refs.canvas);
        p.frameRate(30);
        p.background(255);

        this.mover = new Mover();
      };

      p.draw = () => {
        p.background(255);

        this.mover.display();
        this.mover.update();
      };

      class Mover {
        constructor() {
          this.location = p.createVector(p.random(p.width), p.random(p.height));
          this.velocity = p.createVector(0, 0);
          this.acceleration = p.createVector();
          this.topSpeed = 4;
        }

        update() {
          let mouse = p.createVector(p.mouseX, p.mouseY);
          let dir = P5.Vector.sub(mouse, this.location);

          dir.normalize();
          dir.mult(0.5); // scale
          this.acceleration = dir;

          this.velocity.add(this.acceleration);
          this.velocity.limit(this.topSpeed);
          this.location.add(this.velocity);
        }

        display() {
          let angle = this.velocity.heading();

          p.stroke(0);
          p.fill("#42b983");

          p.push();

          p.rectMode(p.CENTER);
          p.translate(this.location.x, this.location.y);
          p.rotate(angle);
          p.rect(0, 0, 48, 16);

          p.pop();
        }

        checkEdges() {
          if (this.location.x > p.width) {
            this.location.x = 0;
          } else if (this.location.x < 0) {
            this.location.x = p.width;
          }

          if (this.location.y > p.height) {
            this.location.y = 0;
          } else if (this.location.y < 0) {
            this.location.y = p.height;
          }
        }
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight);
      };
    };
    const P5 = require("p5");
    this.ps = new P5(this.script);
    // eslint-disable-next-line
    console.log(this.ps);
  }
};
</script>

<style lang="scss" scoped>
.canvasP5 {
  display: block;
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}
.p5MotionDirection {
  user-select: none;
}
</style>
